	module.exports = {"pathContext":{"docHtml":"<h1>fstream-ignore</h1>\n<p>A fstream DirReader that filters out files that match globs in <code>.ignore</code>\nfiles throughout the tree, like how git ignores files based on a\n<code>.gitignore</code> file.</p>\n<p>Here's an example:</p>\n<pre><code class=\"language-javascript\">var Ignore = require(\"fstream-ignore\")\nIgnore({ path: __dirname\n       , ignoreFiles: [\".ignore\", \".gitignore\"]\n       })\n  .on(\"child\", function (c) {\n    console.error(c.path.substr(c.root.path.length + 1))\n  })\n  .pipe(tar.Pack())\n  .pipe(fs.createWriteStream(\"foo.tar\"))\n</code></pre>\n<p>This will tar up the files in __dirname into <code>foo.tar</code>, ignoring\nanything matched by the globs in any .iginore or .gitignore file.</p>","docPath":"argo-ci/node_modules/fstream-ignore/readme","proj":"argo-ci"}}

